form {
    display: flex;
    flex-direction: column;
    background-color: #F9FBFC;
    padding: 20px;
    width: 90%;
    max-width: 300px;
}

label {
    text-align: center;
    font-weight: bold;
    font-size: 20px;
    color: #1E1E1F;
    margin-bottom: 26px;
}

textarea {
    background-color: #F9FBFC;
    /* border: 2px solid #202329; */
    border-radius: 2px;
    box-shadow: 0 5px 50px rgba(32, 35, 41, 0.25);
    color: #1E1E1F;
    font-size: 20px;
    text-align: center;
    text-align: center;
    padding: 12px;
    height: 96px;
    width: calc(100% - 25px);
}

textarea::placeholder {
    color: #A5A5A5;
    font-family: 'Montserrat';
    font-weight: 400;
}

textarea:focus {
    outline-color: #61DAFA;
}

.TodoForm-buttonContainer {
    display: flex;
    justify-content: space-around;
    padding: 15px 0 0 0;
}

.Todo-Form-button {
    cursor: pointer;
    display: inline-block;
    font-size: 20px;
    color: #202329;
    font-weight: 400;
    width: 120px;
    height: 48px;
    border-radius: 2px;
    border: none;
    font-family: 'Montserrat';
} 

.Todo-Form-button--add {
    background: #61DAFA;
    box-shadow: 0px 5px 25px rgb(97, 218, 250, 0.5);
}

.Todo-Form-button--cancel {
    background: transparent;
}